@use "../foundation/mixin";

// ヘッダー
.header {
  max-width: mixin.$pc;
  margin: 0 auto;
  padding: 15px 20px;
  display: flex;
  align-items: center;
  @include mixin.sp {
    padding: 10px 3%;
  }
}


// ロゴ
.logo {
  width: 140px;
  a {
    display: block;
  }
  &:hover {
    opacity: 0.7;
    transition: 0.1s;
  }
  img {
    width: 100%;
  }
  @include mixin.tab {
    width: 120px;
  }
  @include mixin.sp {
    width: 80px;
  }
}


// ナビ
.menu {
  margin-top: 6px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: auto;
  .menu__item {
    @include mixin.font-l;
    &:nth-child(n + 2) {
      margin-left: 60px;
    }
    a {
      text-decoration: underline;
      &:hover {
        text-decoration: none;
      }
    }
  }
  @include mixin.sp {
    .menu__item {
      &:nth-child(n + 2) {
        margin-left: 30px;
      }
    }
  }
}


// メインビジュアル
.mainVisual {
  max-width: mixin.$pc;
  padding: 0 20px;
  margin: 0 auto;
  @include mixin.sp {
    padding: 0;
  }
  .swiper {
    img {
      width: 100%;
    }
    .swiper-pagination {
      position: relative;
      margin-top: 10px;
      bottom: 0;
    }
    .swiper-slide {
      text-align: center;
      font-size: 18px;
      background: #fff;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .swiper-slide img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    .swiper-button-prev,.swiper-button-next {
      color: #fff;
    }
    .swiper-pagination-bullet-active {
      background-color: #150C37;
    }
    .swiper-button-prev {
      @include mixin.sp {
        left: 0;
      }
    }
    .swiper-button-next {
      @include mixin.sp {
        right: 0;
      }
    }
    .swiper-button-prev:after,.swiper-button-next:after {
      @include mixin.sp {
        font-size: 30px;
      }
    }
  }
}


// セクション
.section {
  margin-top: 80px;
  .section__inner {
    max-width: mixin.$pc;
    padding: 0 20px;
    margin: 0 auto;
  }
  @include mixin.tab {
    margin-top: 70px;
  }
  @include mixin.sp {
    margin-top: 40px;
    .section__inner {
      padding: 0 3%;
    }
  }
}


// 見出し
.headingLv2 {
  @include mixin.font-xl;
  text-align: center;
  border-bottom: 1px #150C37 solid;
}
.headingLv3 {
  @include mixin.font-m;
  font-weight: bold;
  margin-top: 20px;
}


// リードテキスト
.lead {
  @include mixin.font-l;
  text-align: center;
  margin-top: 40px;
  @include mixin.sp {
    margin-top: 25px;
  }
}


// テキスト
.text {
  @include mixin.font-m;
  a {
    text-decoration: underline;
    &:hover {
      text-decoration: none;
    }
  }
}
.text--small{
  @include mixin.font-s;
}


// column 
.column {
  margin-top: 20px;
  img {
    width: 100%;
  }
  .column__item {
    &:nth-child(n + 2) {
      margin-top: 15px;
    }
    & > *:first-child {
      margin-top: 0;
    }
  }
  @include mixin.pc {
    display: flex;
    flex-wrap: wrap;
    margin-top: 40px;
    .column__item {
      width: 48%;
      &:nth-child(n + 2) {
        margin-top: 0;
        margin-left: 4%;
      }
    }
  }
}


// マップ
.map{
  width: 100%;
  height: 350px;
  @include mixin.sp {
    height: 300px;
  }
}


// フッター
.footer {
  border-top: #0A1B4B 1px solid;
  padding: 20px;
  margin-top: 90px;
  .footer__text {
    text-align: center;
    @include mixin.font-xs;
  }
  @include mixin.sp {
    margin-top: 50px;
    padding: 15px;
  }
}


// 汎用クラス
.pc-none {
  display: none;
  @include mixin.sp {
    display: block;
  }
}
.sp-none {
  display: block;
  @include mixin.sp {
    display: none;
  }
}